  max-width: 100%;
}

.horizontal-rule {
  border-top: 1px solid #666666;
  margin-top: 1vmax;
}

.block {
  position: relative;
  background: black;
}

.glow::before,
.glow::after {
  content: "";
  position: absolute;
  left: -2px;
  top: -2px;
  background: linear-gradient(
    45deg,
    yellow,
    red,
    purple,
    blue,
    cyan,
    green,
    lime
  );
  width: calc(100% + 5px);
  height: calc(100% + 5px);
  z-index: 1;
  -webkit-animation: animate 20s linear infinite;
          animation: animate 20s linear infinite;
  background-size: 200%;
}

@-webkit-keyframes animate {
  0% {
    background-position: 0 0;
  }
  50% {
    background-position: 400% 0;
  }
  100% {
    background-position: 0 0;
  }
}

@keyframes animate {
  0% {
    background-position: 0 0;
  }
  50% {
    background-position: 400% 0;
  }
  100% {
    background-position: 0 0;
  }
}

.glow::after {
  -webkit-filter: blur(20px);
          filter: blur(20px);
}

.buildFont {
  font-family: Helvetica, sans-serif;
  color: #dbdbdb;
}

.box {
  position: -webkit-sticky;
  padding: 5px;
  position: sticky;
  top: 0;
  background: #1e2125;
  z-index: 1;
  border-radius: 5px;
}

.price {
